import Vue from 'vue'
import Router from 'vue-router'

// 自定义引入
// 前台部分
import Index from "./views/Index.vue"
import NotFound from "./views/NotFound.vue"
import Login from "./components/FrontLogin/Login.vue"
import Register from "./components/FrontLogin/Register.vue"
import ProductDetail from './components/ProductDetail/ProductDetail.vue'
import PersonalInfo from './components/PersonalInfo/PersonalInfo.vue'
// 后台部分
// import AdminInterface from "./views/AdminInterface.vue"
import MainPage from "./components/AdminInterface/MainPage.vue"
import AdminLogin from "./components/AdminLogin/AdminLogin.vue"
// import QueryUser from "./components/AdminInterface/UserManage/QueryUser.vue"
import AddUser from "./components/AdminInterface/UserManage/AddUser.vue"
import QueryProducts from "./components/AdminInterface/ProductsManage/QueryProducts.vue"
import AddProducts from "./components/AdminInterface/ProductsManage/AddProducts.vue"
import QueryCategory from "./components/AdminInterface/CategoryManage/QueryCategory.vue"
import AddCategory from "./components/AdminInterface/CategoryManage/AddCategory.vue"
import QueryOrder from "./components/AdminInterface/OrderManage/QueryOrder.vue"


Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/test',
            component: () =>
                import ('./views/Test.vue')
        },
        {
            path: '/',
            name: 'home',
            redirect: '/index'
        },
        {
            path: '/index',
            name: 'index',
            component: Index
        }, {
            path: '/adminlogin',
            name: 'adminlogin',
            component: AdminLogin
        },
        {
            path: '/admininterface',
            name: 'admininterface',
            component: () =>
                import ('./views/AdminInterface.vue'),
            redirect: '/admininterface/mainpage',
            children: [{
                    path: 'mainpage',
                    name: '主页',
                    component: MainPage
                },
                {
                    path: 'usermanage/queryuser',
                    name: '查询用户',
                    // 在首部导入无法修改element样式
                    component: () =>
                        import ('./components/AdminInterface/UserManage/QueryUser.vue')
                },
                {
                    path: 'usermanage/adduser',
                    name: '添加用户',
                    component: AddUser
                },
                {
                    path: 'productsmanage/queryproducts',
                    name: '查询商品',
                    component: QueryProducts
                },
                {
                    path: 'productsmanage/addproducts',
                    name: '添加商品',
                    component: AddProducts
                },
                {
                    path: 'categorymanage/querycategory',
                    name: '查询分类',
                    component: QueryCategory
                }, {
                    path: 'categorymanage/addcategory',
                    name: '添加分类',
                    component: AddCategory
                },
                {
                    path: 'ordermanage/queryorder',
                    name: '查询订单',
                    component: QueryOrder
                }
            ]
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/register',
            name: 'register',
            component: Register
        },
        {
            path: '/products',
            name: 'products',
            component: () =>
                import ('./components/Index/mProduct.vue')
        },
        {
            path: '/productdetail',
            name: 'productdetail',
            component: ProductDetail
        },
        {
            path: '/shopcar',
            name: 'shopcar',
            component: () =>
                import ('./components/ShopCar/ShopCar.vue')
        },
        {
            path: '/personalinfo',
            name: 'personalinfo',
            component: PersonalInfo
        },
        {
            path: '/nav',
            component: () =>
                import ("./components/Common/NavgationBar.vue")
        },
        {
            path: '*',
            name: 'notfound',
            component: NotFound
        }
    ]
})