<template>
<div class="personalinfo">
  
    <el-card class="box-card">
  <div slot="header" class="clearfix">
    <el-page-header @back="$router.back()" content="个人信息"></el-page-header>
    <el-button style="float: right; padding: 3px 0" type="text" @click="dialogFormVisible = true">修改</el-button>

  </div>
       <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1560530157730&di=be8f72350598b176b3088d18cdfbe826&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fphotoblog%2F1406%2F20%2Fc2%2F35489246_1403250875219_mthumb.jpg" class="image" style="width:860px;height:250px">
  <el-form :model=list>
    <el-form-item label="用户id:" :label-width="formLabelWidth">
      <el-input v-model="list.userId" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户名:" :label-width="formLabelWidth">
      <el-input v-model="list.userUserName" autocomplete="off" ></el-input>
    </el-form-item>
    <el-form-item label="用户电话:" :label-width="formLabelWidth">
      <el-input v-model="list.userTelphone" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户地址:" :label-width="formLabelWidth">
      <el-input v-model="list.userAddress" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户商店名:" :label-width="formLabelWidth">
      <el-input v-model="list.userShopName" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户简介:" :label-width="formLabelWidth">
      <el-input v-model="list.userComment" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
 
</el-card>
 
    
     <el-dialog title="用户信息" :visible.sync="dialogFormVisible">

  <el-form :model=list>
    <el-form-item label="用户id" :label-width="formLabelWidth">
      <el-input v-model="list.userId" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户名" :label-width="formLabelWidth">
      <el-input v-model="list.userUserName" autocomplete="off" ></el-input>
    </el-form-item>
    <el-form-item label="用户密码" :label-width="formLabelWidth">
      <el-input v-model="list.userPassword" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户电话" :label-width="formLabelWidth">
      <el-input v-model="list.userTelphone" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户地址" :label-width="formLabelWidth">
      <el-input v-model="list.userAddress" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户商店名" :label-width="formLabelWidth">
      <el-input v-model="list.userShopName" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="用户简介" :label-width="formLabelWidth">
      <el-input v-model="list.userComment" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogFormVisible = false;open()">确 定</el-button>
  </div>
</el-dialog>
</div>
</template>
<script>
export default {
    name:"info",
    data(){
        return{
            list : [],
            list1:'',
            dialogTableVisible: false,
            dialogFormVisible: false,
            formLabelWidth: '120px',
            userId : this.$route.params.userId
        }

    },
    created(){
        this.init();
        this.update()
    },
    
    methods:{
        init() {
          this.$axios
            .get("http://www.wxhwg.com:8080/mock/api/user/findById", {
              params: {
                userId: this.userId
              }
            })
            .then(response => {
              let result = response.data;
              if (result.status == "200") {
                  this.list=result.data;
                let temp = [];
                if(result.data != null){
                  temp.push(result.data);
                }
                this.list1 = temp;
              
              }
            })
            .catch(error => {
              console.log(error);
            });
        },
 
         update(){
          this.$axios({
            method:'post',
            url:'http://www.wxhwg.com:8080/mock/user/update',
            data:this.$qs.stringify({
               userId	:list.userId,
               userUserName:list.userUserName,
               userPassword:list.userPassword,
               userTelphone:list.userTelphone,
               userAddress:list.userAddress,
               userShopName:list.userShopName,
               userComment:list.userComment
              })
            }).then(function (response) {
                var result=response.data;
                if(result.status=500){
                  
                  this.$alert(result.message);

                }else{
                  this.$alert("修改成功");
                }
              
                }).catch(function (error) {
                  console.log(error)
                })
      },
       open() {
        this.$confirm('此操作将修改信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '修改成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      }
    
          


        
    }
}
</script>




<style scoped>
  .table{
    background-color: aqua;
    margin: 0 auto 0;
  }
  #table tr{
   height: 150px;
   width: 50%;
  }
  #table tr td{
width: 90%;
  }
   .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 900px;
    margin: 40px auto;
    background-color: #fff;
  }
</style>
