<template>
<div>
    <el-dialog width="40%" title="添加商品"  :visible.sync="show1">
        <i-upload @messageData="getData"></i-upload>
    </el-dialog>


    <el-dialog
   
    title="提示"
    width="40%"
    :visible.sync="dialogVisible">
        <div style="width:230%">
        <i-fromUp v-bind:transmits="transmit" @messageData="returnStatus"></i-fromUp>
        </div>
    </el-dialog>



    <el-table
    v-bind:data="title"
    style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
     <el-form label-position="left" inline class="demo-table-expand">
        <el-form-item label="商品品牌">
        <el-input    :disabled="true"
            v-bind:title="props.row.productId" 
 v-bind:placeholder="props.row.productBrandId" v-model="props.row.productBrandId">{{ props.row.productBrandId }}</el-input>
        </el-form-item>

        <el-form-item label="缺货状态">
        <el-dropdown  @command="setShortage">
        <span class="el-dropdown-link" v-if="props.row.productStatus == 1 ? status='缺货' : status='有货'">
        {{status}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
        <el-dropdown-item :command='{status:1,productId:props.row.productStatus}'>缺货</el-dropdown-item>
        <el-dropdown-item :command='{status:0,productId:props.row.productStatus}'>有货</el-dropdown-item>
        </el-dropdown-menu>
        </el-dropdown>
        </el-form-item>
        <el-form-item label="类别编号">
            <el-input :disabled="true" v-bind:placeholder="props.row.productCategoryId" v-model="props.row.productCategoryId">{{ props.row.productCategoryId }}</el-input>
        </el-form-item>
        <el-form-item label="是否在销">
        <el-input   :disabled="true"   v-if="props.row.productIsSale==1?sale='在售':sale='废弃'" v-bind:placeholder="props.row.productIsSale" v-model="sale"></el-input>  
        </el-form-item>

        <el-form-item label="上架时间">
            <el-input :disabled="true" v-bind:placeholder="props.row.productDateTime" v-model="props.row.productDateTime">{{ props.row.productDateTime }}</el-input>
        </el-form-item>
          <el-form-item label="商品状态">
          <el-dropdown  @command="setDelete" v-bind:titie="props.row.productId">
          <span class="el-dropdown-link" v-if="props.row.productStatus?status1='在售':status1='废弃'">
          {{status1}}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
          <el-dropdown-item :command='{status:1,productId:props.row.productId}'>在售</el-dropdown-item>
          <el-dropdown-item :command='{status:0,productId:props.row.productId}'>废弃</el-dropdown-item>
          </el-dropdown-menu>
          </el-dropdown>       
          </el-form-item>

        <el-form-item label="建议售价">
            <el-input :disabled="true" v-bind:placeholder="props.row.productSuggestPrice" v-model="props.row.productSuggestPrice">{{ props.row.productSuggestPrice }}</el-input>
        </el-form-item>
        <el-form-item label="商品图片">
              <img   @click="enlarge"  v-bind:src="(path)+props.row.productPhoto" v-bind:title="props.row.productId"  min-width="40" height="40" />
        </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="商品 ID"
      prop="productId">
    </el-table-column>
    <el-table-column
      label="商品名称"
      prop="productName">
    </el-table-column>
    <el-table-column
      label="描述"
      prop="productPhoto">
       <template   slot-scope="scope"> 
                    <el-popover
                    placement="top-start"
                    width="300"
                    trigger="hover"
                    > 
           <img slot="reference"  v-bind:src="(path)+scope.row.productPhoto" min-width="40" height="40" />           
           <img   v-bind:src="(path)+scope.row.productPhoto"  width="300" height="300" />           
                   </el-popover>
       </template> 
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>
<script>
// 上传图片
import upload from './upload.vue'
// 新增商品
import fromUp from './AddProducts.vue'
export default {
    components: {
    "i-upload":upload,
    "i-fromUp":fromUp
  },
    props:["title"],
       data(){
        return{
          dialogVisible:false,
          transmit:[],
          status:'',
          sale:'',
          photo:'',
          show1:false,
          ProductId:'',
          path:'http://www.wxhwg.com:8080/mock/static/'
        }
    },
  
    methods:{
      handleEdit(index, row){
        this.transmit=this.title[index];
        this.dialogVisible=true;
      },
      handleDelete(index, row){
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // 删除本地当前行
          this.title.splice(index,1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      },
        //-----------商品作废 删除
    setDelete(command){
        let self=event;
        if(command.status=='0'){
        this.$axios({
            method:'get',
            url:'http://www.wxhwg.com:8080/mock/product/drop',
            params:{
                productId:command.productId
            }
        }).then(response=>{
         this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
        })
     }
     },

     //---------------缺货设置
     setShortage(command){
         if(command.status=='0'){
             //重新上货
            this.$axios({
                  method:'get',
                  url:'http://www.wxhwg.com:8080/mock/product/noLack',
                  params:{
                      id:command.productId
                  }
              }).then(response=>{
                    this.$message({
                    message:response.data.message,
                    center:true,
                    duration:1000,
                    type:1000,
                  })
              })
         }else if(command.status=='1'){
             // 设置缺货
          this.$axios({
            method:'get',
            url:'http://www.wxhwg.com:8080/mock/product/lack',
            params:{
                id:command.productId
            }
        }).then(response=>{
          this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })

        })
     }
     },
     returnStatus(){
       this.dialogVisible=false;
     },
      getData(val){
        let self=this;
        self.$axios({
       method:'get',
       url:'http://www.wxhwg.com:8080/mock/product/photo',
       params:{
         productId: self.ProductId,
         photo:val,
       }
     }).then(response=>{
         self.$message({
                message:response.data.message,
                center:true,
                duration:1000,
                type:1000,
              })
     self.show1=false;
       })  
      },
     //  修改图片 只能修改固定图片
    enlarge(){
     this.ProductId=event.target.title;
     this.show1=true
    //  let self=this;
    //     self.$axios({
    //    method:'get',
    //    url:'http://www.wxhwg.com:8080/mock/product/photo',
    //    params:{
    //      productId: self.ProductId,
    //      photo:'1560523395762.jpg'//val,//1555156735685.jpg
    //    }
    //  }).then(response=>{
    //      self.$message({
    //             message:response.data.message,
    //             center:true,
    //             duration:1000,
    //             type:1000,
    //           })
    //  self.show1=false;
    //    })  
    },
    }
}
</script>
<style scoped>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>
