<template slot-scope="xxx">
    <div class="queryorder">

    <!-- 顶部查询框和查询按钮 -->

      <div>
        <el-row>
            <el-button type="primary" style="width:110px;float:left;margin:10px" @click="exportExcel()" >导出全部数据</el-button>
            <el-button type="primary" style="width:60px;float:left;margin:10px" @click="queryAll()" >刷新</el-button>

            <div class="fright" style="margin:10px">
                <el-input ref="input" v-model="str" placeholder="请输入订单id进行查询" style="width:200px "></el-input>
                <el-input ref="input" v-model="shopName" placeholder="请输入商店名称进行查询" style="width:200px "></el-input>
                <el-button type="primary" style="width:100px" @click="queryAll()" >查询</el-button>
            </div>
        </el-row>
      </div>
      

      <!-- 展开详情 table 主题 -->
      <div style="odertable">
        <el-table :data="tableData" id="table" style="width: 100%; margin:10px" >
          <el-table-column  type="expand"  >
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <div style="float:left"> 
                  <el-form-item label="订单id:">
                    {{props.row.orderId}}
                  </el-form-item><br>
                  <el-form-item label="订单用户id:">
                    {{props.row.orderUserId}}
                  </el-form-item><br>
                  <el-form-item label="订单是否发送:">
                    {{props.row.orderIsSend}}
                  </el-form-item><br>
                  <el-form-item label="订单是否有效:">
                    {{props.row.orderIsValid}}
                  </el-form-item><br>
                  <el-form-item label="订单总计:">
                    {{props.row.orderTotalPrice}}
                  </el-form-item><br>
                  <el-form-item label="订单状态:" >
                    {{props.row.orderStatus}}
                  </el-form-item><br>
                  <el-form-item label="订单评论:">
                    {{props.row.orderComment}}
                  </el-form-item><br>
                  </div>
                  <div style="float:right">
                  <el-form-item label="用户姓名:" >
                    {{props.row.user.userUserName}}
                  </el-form-item><br>
                  <el-form-item label="商店 名称:">
                    {{props.row.user.userShopName}}
                  </el-form-item><br>
                  <el-form-item label=" 商店地址:" >
                    {{props.row.user.userAddress }}
                  </el-form-item><br>
                  <el-form-item label="商店评价:">
                    {{props.row.user.userComment}}
                  </el-form-item><br>
                  <el-form-item label="商店状态:">
                    {{props.row.user.userStatus}}
                  </el-form-item><br>
                  <el-form-item label="用户 ID:">
                    {{props.row.orderUserId}}
                  </el-form-item><br>
                  </div>
                </el-form>
            </template>
          </el-table-column>


              <!-- 表格查询所有订单信息 -->
                
          <el-table-column prop="orderId" label="订单id" title="orderId"  width="180"> </el-table-column>
          <el-table-column prop="orderDateTime" label="订单添加时间" width="180"> </el-table-column>
          <el-table-column prop="orderTotalPrice" label="总价" width="180"> </el-table-column>
          <el-table-column prop="user.userShopName" label="商店名称" width="180"> </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <span >
                  <el-button type="mini"  @click="queryByid(scope.row.orderId)" v-bind:title="scope.row.orderId" >商品详情</el-button>
                  <el-button style="width:60px; margin-right:10px;" type="mini" @click="send(scope.row,scope.$index)" v-bind:title="scope.row.orderId" v-if="scope.row.orderIsSend=='0'" v-text="btntext" v-show="show"></el-button>
                  <el-button style="width:60px; margin-right:10px;" type="mini" @click="sendcom(scope.row.orderId,scope.$index) " v-bind:title="scope.row.orderId" v-if="scope.row.orderIsSend=='1 '"  v-text="btntext1"></el-button>
              </span>
            <el-dialog title="详情"  :visible.sync="dialogTableVisible">

                <!-- 查询商品详情 -->

                <el-table :data="list">
                  <el-table-column property="product.productName" label="名称" width="150"></el-table-column>
                  <el-table-column property="detailDateTime" label="商品添加时间" width="200"></el-table-column>
                  <el-table-column property="detailProductCount" label="购买数量"></el-table-column>
                  <el-table-column property="detailProductPrice" label="单价"></el-table-column>
                    <el-table-column property="detailTotalPrice" label="总价"></el-table-column>
                    <el-table-column property="detailProductPrice" label="图片">
                      <template slot-scope="scope">
                        <img  v-bind:src=src+scope.row.product.productPhoto alt="" style="width: 50px;height: 50px">
                      </template>
                    </el-table-column>
                    <!--  <el-table-column property="detailUserId" label="购买人"></el-table-column> -->
                </el-table>
            </el-dialog>
                      <!-- 删除按钮 ------------------------------------->
              <el-button size="mini" type="danger" @click="handleDelete(scope.row.orderId,scope.$index)">废弃</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页 -->
        <el-row>
            <el-col :span="24">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page.sync="currentPage1"
                  :page-sizes="[10, 20, 30, 40]"
                  :page-size="size"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="length">
                </el-pagination>
            </el-col>
        </el-row>
      </div>


  </div>
</template>


<script>
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
   name:'products',
      data(){
         return {
           BtnName:'发货',
           name:[],
           var:'',
           str:'',
           size:"10",
           shopName:'',
           list:'',
           list2:"",
           src:"http://www.wxhwg.com:8080/mock/static/",
           web_static_server:'http://www.wxhwg.com:8080/mock/static/',     
           length:'',
           currentPage1: 1,
          // total: 1,
           page: 1,
           btntext:"发货",
           btntext1:"已发货",
           show:true,
           pageNum:1,
           mingxiid:'',
           yema:{
             currentPage:1,
             pageSize1:10,
            },
           
           /* 表格绑定 */
           tableData: [{
                orderId: '',
                orderDateTime: '',
                orderTotalPrice: '',
                orderStatus: '',
                orderIsValid: 1,
                orderComment: null,
                user: {
                userId: "",
                userUserName: "",
                userPassword: "",
                userTelphone: "",
                userAddress: "",
                userShopName: "",
                userComment: "",
                userStatus: ''
                }
             }],
           dialogTableVisible: false,
        }
      },
      props:{
            category:String,  //父组件向子组件传参
            title:String
      },
     
      created(){
        this.queryAll()
      },
      methods:{

       /* 删除---------------------------------------- */

       handleDelete(str,index){
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示',{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
          }).then(() => {
             this.$axios.get("http://www.wxhwg.com:8080/mock/order/invalid",{
                    params:{
                      orderid:str, 
                    }
              }).then(response=>{ 
                let  result =response.data;
                  if(result.status=="200"){           
                      this.$message({
                       type: 'success',
                       message: '删除成功!', 
                      });
                      this.tableData.splice(index,1);
                  }
              }).catch(error=>{ 
                })
          }).catch(() => {
           this.$message({
             type: 'info',
             message: '已取消删除'
            });       
          });       
      },



       /* 查询所有------------------------------------------ */
       
       queryAll(){

          this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum:'1', 
                        pageSize:'1000',
                        orderId:this.str, 
                        shopName:this.shopName,
                        /* shopName:this.shopName,  */
                    }
          }).then(response=>{
                 
            let  result =response.data;
            if(result.status=="200"){
              this.length=result.data.list.length;
              this.tableData=result.data.list;
              this.tableData=this.tableData.splice(0,10);
              
            }
          }).catch(error=>{})
                
        }, 

       // -------------------发货
       send(id,index){
         this.$confirm('确认发货?', '提示', {
           confirmButtonText: '确定',
           cancelButtonText: '取消',
           type: 'warning'
          }).then(() => {
           this.$axios.get("http://www.wxhwg.com:8080/mock/order/send",{
                params:{
                  orderid:id.orderId, 
                }
            }).then(response=>{
                let  result =response.data;
                if(result.status=="200"){
                     
                    this.$message({
                      type: 'success',
                      message: '发送成功!',
                     
                    });
                    id.orderIsSend=1
                   
                  }else{
                    this.$message({
                      type: 'success',
                      message: '发送失败!'
                    });
                  }
            }).catch(error=>{})
        
          }).catch(() => {
            this.$message({
             type: 'info',
             message: '已取消删除'
            });          
         });
        },
        sendcom(){
          this.$alert('此订单已发送，请勿重复提交', '发送失败', {
            confirmButtonText: '确定',
            callback: action => {}
          });
        },

        /* <!-- 分页------------------------------------------------ --> */

        handleSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        
        this.yema.pageSize1 = val;
         this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum: this.yema.currentPage, 
                        pageSize:val,  
                    }
            }).then(response=>{
                        let  result =response.data;
                        if(result.status=="200"){
                          this.tableData=result.data.list;
                          let orderIsSend=result.data.list.orderIsSend
                          if(this.tableData.length!=val){
                            var num =val-this.tableData.length;
                            for(var index=0;index<num;index++){}
                          }
                        }else{
                           alert(result.message);
                         }
              }).catch(error=>{})
        },
        handleCurrentChange(val) {
          this.yema.currentPage = val;
        
          this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum:val, 
                        pageSize: this.yema.pageSize1,  
                    }
            }).then(response=>{
                        let  result =response.data;
                        if(result.status=="200"){
                          this.tableData=result.data.list;
                          let orderIsSend=result.data.list.orderIsSend
                          if(this.tableData.length!=10){
                            var num =10-this.tableData.length;
                            for(var index=0;index<num;index++){}
                          }
                        }else{
                           alert(result.message);
                         }
              }).catch(error=>{})
        },
        getDetails(row){
         this.mingxiid=row.orderId
        },

    /* 按id查询----------------------------------- */
        queryByid(id){
          this.dialogTableVisible = true
          this.$axios.get("http://www.wxhwg.com:8080/mock/order/detail",{
                    params:{
                      orderid:id
                    }
            }).then(response=>{
                let  result =response.data;
                if(result.status=="200"){
                 this.list=result.data;  
                }
              }).catch(error=>{})
       },
       /* 导出数据 -------------------------*/
       exportExcel(){
           /* generate workbook object from table */
            let wb = XLSX.utils.table_to_book(document.getElementById('table'));
            /* get binary string as output */
            let wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' });
            try {
              FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '订单表.xlsx');
            } catch (e){
              if (typeof console !== 'undefined')
              console.log(e, wbout)
            }
            return wbout
        }
    
     }
 }
</script>



<style scoped>

    .el-input{
      margin-right: 10px;
    }
</style>
