// 搜索栏组件
<template>
    <div class="herderSerch">
        <el-input style="width:300px"
            placeholder="请输入商品名称"
            v-model="name"
            clearable>
        </el-input>
        <el-button type="primary" @click="search()" style="width:80px;"><i class="el-icon-search"></i>搜 索</el-button>
    </div>
</template>

<script>
import publicVuex from './publicVuex.js';
export default {
    data() {
        return {
            mlist:[],
            name:''
        };
    },
    methods: {
        search(){
            let self=this;
            this.$axios({
                method : 'get',
                url : 'http://www.wxhwg.com:8080/mock/api/product/search',
                params : {
                    name : self.name
                }
            }).then(response=>{
                let result=response.data;
                if(result.status=='200'){
                    self.mlist=result.data;
                    // this.$store.state.mlist('search',result.data);
                    // this.$router.push({   
                    //   name:"index"
                    // })
                    this.$router.push({path:'/products',query:{prolist:this.name}})
                }
            }).catch(error=>{
                alert(error);
            });
            publicVuex.$emit("to-list",this.mlist);
        }
    }
}
</script>
<style>
    .herderSerch{
        width: 100%;
        height: 100px;
        padding-top: 40px;
        text-align: center;
        font-size: 20px;
    }
</style>
