<template>
<div class="nav" >
    <div class="nav-title">
        <p>商品分类</p>  
        <h2>{{title}}</h2>
    </div>
    <div id="left_nav" >
        <el-tabs :tab-position="tabPosition" >
            <el-tab-pane v-for="item in clist" :key='item.categoryId' v-bind:label="item.categoryName">
                <i-proshow v-bind:title='item.categoryId'></i-proshow>
            </el-tab-pane>
        </el-tabs>
    </div> 
</div> 

</template>

<script>
 import ProductsShowcase from './ProductsShowcase.vue'
 import publicVuex from './publicVuex.js';

export default {
    name:'products',
    data(){
        return{
            tabPosition: 'top',
            web_static_server:'http://www.wxhwg.com:8080/mock/static/',
            clist:[],
        }
    },
    props:{
        category:String,
        title:String
    },
    components:{
        'i-proshow':ProductsShowcase
    },
    created(){
        this.cinit();
    },
    methods:{
     
      
        cinit(){
            this.$axios.get("http://www.wxhwg.com:8080/mock/api/category/list",{
                params:{
                    categoryId:this.category,
                    pageSize:8
                    
                }
            }).then(response=>{
                let result=response.data;
                if(result.status==200){
                    this.clist=result.data;
                }
            }).catch(error=>{

            });
        },
        
        show(id){
            //push带有后退结果  replace没有后退
            this.$router.push({
                path:'product',
                query:{id}
            })
        }
    }
}
</script>
<style scoped>
.nav-title{
     width: 100px;
     height: 50px;
     font-size: 18px;
     font-family: '微软雅黑';
    
}
.nav{
    height: 900px;
}
