<template>
    <div class="mainpage">
        <!-- 临时解决方案，应该和admininterface同步 -->
        <p>当前用户：<span>{{ this.currentUserName }}</span></p>
        <!-- 折线图 -->
        <div class="fleft" id="chartmainline" style="width:600px; height:400px;"></div>
        <!-- 柱状图 -->
        <div class="fleft" id="chartmainbar" style="width:600px; height:400px;"></div>
    </div>
</template>


<script>
export default {
    data() {
        return {
            currentUserName: 'null',
            optionline:{
                title:{
                    text:'2019年度  季度营业收入'
                },
                tooltip:{},
                legend:{
                    data:['用户来源']
                },
                xAxis:{
                    data:["第一季度","第二季度","第三季度","第四季度"]
                },
                yAxis:{
 
                },
                series:[{
                    name:'访问量',
                    type:'line', //设置图表主题
                    data:[0,5037.60,0,0]
                }]
            },
            optionbar:{
                title:{
                    text:'2019年度  商品销售排行'
                },
                tooltip:{},
                legend:{
                    data:['用户来源']
                },
                xAxis:{
                    data:["22g盐津铺子拉撕素肉","1元同食轩竹笋","18g勇煌馋嘴牛肉","22g勇煌馋嘴牛肉",
                    "0.5元伤心凉皮","18g勇煌辣子鸡","20g盐津铺子鱼豆腐","106g盐津铺子小新王子"]
                },
                yAxis:{
 
                },
                series:[{
                    name:'访问量',
                    type:'bar', //设置图表主题
                    data:[45884,36660,35506.00,23656.00,10535.00,8050.00,5360.00,2750.00]
                }]
            }
        }
    },
    created(){
        this.currentUserName = this.$store.state.loginAdminId;
        this.inite();
    },
    mounted() {
       
        this.drawLine();
    },
    methods: {
        inite(){
            // 2019年度统计每个季度营业收入
            this.$axios.get("http://www.wxhwg.com:8080/mock/order/selectTop",{
                params : { year : 2019 }
            }).then(response=>{
                let result = response.data;
                if(result.state = "200"){
                    // 更新图标数据
                    for(let i = 0;i < result.data.length;i++){
                        this.optionline.xAxis.data.push(result.data[i].MONTHS);
                    }
                    // 此处能获取，但无法改变原数组
                    
                }else{
                    console.log("查询统计失败");
                }   
            }).catch(error=>{
                alert("查询图表"+error);
                console.log(error);
            });

            
            // 按年度统计商品销售排行
      },
      drawLine: function(){
        //基于准本好的DOM，初始化echarts实例
        let chartmainline = this.$echarts.init(document.getElementById("chartmainline"));
        let chartmainbar = this.$echarts.init(document.getElementById("chartmainbar"));

        //绘制图表
        chartmainline.setOption(this.optionline);
        chartmainbar.setOption(this.optionbar);
      }
      
    }
}
</script>


