<template>
<div class="proshowcase">
    <div class="probox" >
        <el-row  >
        <el-col v-for="item in pageSizess" :key="item.productId" style="height:350px;width:260px; padding:5px">
            <el-card shadow="hover" :body-style="{ padding: '7px',width:'240px',height:'320px'}">
                    
                <router-link :to="{path : '/productdetail', query : {id : item.productId }}">
                    <img v-bind:src="web_static_server + item.productPhoto" class="image">
                </router-link>

                <div style="padding:0px;height:90px;">
                    <span style="font-size: 15px;">{{item.productName}}</span><br>
                    <span style="color:red; ">￥{{item.productSmallPrice}}元</span>
                    <div class="bottom clearfix">
                        <el-button @click.native="AddShopCar(item.productId)" type="text" class="button" >加入购物车</el-button>
                    </div>
                </div>
            </el-card>
        </el-col>
    </el-row>
    </div>
    <div class="pag">
        <!-- <el-pagination
            :page-size="8"
            :pager-count="11"
            layout="prev, pager, next"
            :total="total"
            @current-change="pager"
            @prev-click='prev'
            @next-click='next'>
        </el-pagination>  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="pager"
            :current-page="number"
            :page-sizes="[8, 16, 32, 64]"
            :page-size="8"
            @prev-click='prev'
            @next-click='next'
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>    
    </div>
    <!-- <div class="product" v-for="item in pageSizes" :key="item.productId">
        <el-image class="img" :src="web_static_server+item.productPhoto">
            <div slot="placeholder" class="image-slot">
                加载中<span class="dot">...</span>
            </div>
        </el-image>
        <div class="information">
            <p>{{item.productName}}</p>
            <p>{{item.productSmallPrice}}元</p>    
        </div>
    </div>  -->
        <!-- <div style="clear:both">共{{lisLength}}条数据   共{{sum}}元</div> -->
</div>

</template>
<script>
export default {
    name:'proshowcase',
    data(){
        return{
            web_static_server:'http://www.wxhwg.com:8080/mock/static/',
            total:0,
            tlt:'',
            pageNum:'',
            number:1,
            pageSizess:[],
            currentPage1: 5,
            currentPage2: 5,
            currentPage3: 5,
            currentPage4: 4
        }
    },

    computed:{
        list:function(){
            return this.$store.state.list
        }
    },
    props:["pageSizes","title"],
    created(){
        this.tlt=this.title;
        this.pageSizess=this.pageSizes;
        this.init();
    },
    methods:{
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        pager(num){
            this.number=num;
            this.init();
        },
        prev(num){
             this.number=num;
            this.init();
        },
        next(num){
            this.number=num;
            this.init();
        },
        init(){
            //  let self=this;
            // publicVuex.$on("to-list",function(data){
            //     self.pageSizes=data;
            // })
            let self=this;
            //alert(self.title)
            self.$axios.get("http://www.wxhwg.com:8080/mock/api/product/selectByCatrgory",{
                params:{
                    categoryId:self.tlt,
                    pageNum:self.number,
                    pageSize:8
                }
            }).then(response=>{
                let result=response.data;
                if(result.status==200){
                    self.pageSizess=[];
                    self.pageSizess=result.data.list;
                    self.total=result.data.total;
                    // console.log(this.total)
                }
            }).catch(error=>{
                console.log(error);
            });
        },
        
        show(id){
            //push带有后退结果  replace没有后退
            this.$router.push({
                path:'product',
                query:{id}
            })
        },
        // 添加购物车
        AddShopCar(productId){
            let user = JSON.parse(this.$store.state.loginUser);
            console.log(uuid)
            if(!user){
                var mymessage=confirm("请先登录账号！");
                if(mymessage==true){   
                   this.$router.push({path:'/login'});
                }
                return;
            }
            let uuid = user.userId;
            // 查询单个商品信息
            this.$axios
            .get("http://www.wxhwg.com:8080/mock/api/product/find", {
                params: {
                    productId : productId
                }
            })
            .then(response => {
                let result = response.data;
                if (result.status == 200) {
                    let currentProduct = result.data;
                    
                    // 加入购物车
                    this.$axios({
                        method: "post",
                        url: "http://www.wxhwg.com:8080/mock/api/detail/join",
                        data: this.$qs.stringify({
                            detailProductId: currentProduct.productId, // 传参
                            detailProductCount: 1,
                            detailProductUnit: currentProduct.productLargerUnit,
                            detailProductPrice: currentProduct.productSmallPrice,
                            detailComment: "",
                            detailUserId: uuid
                        })
                    }).then(res => {
                        let result = res.data;
                        if (result.status == 200) {
                            console.log(result.message);
                            this.$message.success("加入购物车成功！");
                        } else {
                            this.$message.warning("加入购物车失败！");
                        }
                    });

                }
            })
            .catch(error => {
                alert(error);
            });
            
        }








    }
}
</script>
<style scoped>

