     width:250px; 
     height:320px;
     /* background-color:powderblue; */
     /* float: left;
     margin: 5px 5px 5px 5px;
     border: 1px solid slateblue;
}   */ 
.proshowcase{
    width: 1200px;
    /* border: 1px solid slateblue; */
    text-align: center;
    background-color: #f6f6f7
}

.probox .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

.probox .button {
    padding: 0;
    float: right;
  }
.probox span{
    padding-bottom:7px;
}
.probox  .image {
    padding-top:10px;
    padding-bottom:5px;
    padding-left: 5px; 
    padding-right: 5px;
    width: 93%;
    height: 220px;

    display: block;
  }

 .probox .clearfix:before,
 .probox .clearfix:after {
      display: table;
      content: "";
  }
  
.probox .clearfix:after {
      clear: both
  }

</style>

