<template>
    <div class="adminlogin">

       <div class="cover" ref="homePage">
            <div class="maincotent">
                <div class="logo-box">
                    <div class="login">
                        <div class="bxs-row" style="text-align:center;">
                            <img id="logo" src="@/assets/image/verify/logo.png" style="width:72px;"><span class="tips"><h3 style="color:#333;">商城后台管理系统</h3></span>
                        </div>
                        <div class="bxs-row">
                            <input v-model="username" type="text" class="username" placeholder="用户名">
                            <p class=" err err-username"></p>
                        </div>
                        <div class="bxs-row">
                            <input v-model="password" type="password" class="password" placeholder="密码">
                            <p class="err err-password"></p>
                        </div>
                        <div class="bxs-row">
                            <input type="submit" class="submit btn" v-on:click="verify" value="登录">
                        </div>
                    </div>
                    <div class="verBox">
            <div id="imgVer" style="display:inline-block;"></div>
        </div>
    </div>
            </div>
        </div>

    </div>
</template>


<script scoped>
import {} from '@/assets/js/jquery.min.js'
export default {
    data(){
        return {
            clientHeight:'',
            username:'',
            password:''
        }
    },
    mounted(){
        // 获取浏览器可视区域高度
        this.clientHeight =   `${document.documentElement.clientHeight}`          
        window.onresize = function temp() {
            this.clientHeight = `${document.documentElement.clientHeight}`;
        };


        function imgVer(Config) {

            var el = eval(Config.el);
            var w = Config.width;
            var h = Config.height;
            var imgLibrary = Config.img;
            var PL_Size = 48;
            var padding = 20;
            var MinN_X = padding + PL_Size;
            var MaxN_X = w - padding - PL_Size - PL_Size / 6;
            var MaxN_Y = padding;
            var MinN_Y = h - padding - PL_Size - PL_Size / 6;

            function RandomNum(Min, Max) {
                var Range = Max - Min;
                var Rand = Math.random();
                if (Math.round(Rand * Range) == 0) {
                    return Min + 1;
                } else if (Math.round(Rand * Max) == Max) {
                    return Max - 1;
                } else {
                    var num = Min + Math.round(Rand * Range) - 1;
                    return num;
                }
            }
            var imgRandom = RandomNum(1, imgLibrary.length);
            var imgSrc = imgLibrary[imgRandom];
            var X = RandomNum(MinN_X, MaxN_X);
            var Y = RandomNum(MinN_Y, MaxN_Y);
            var left_Num = -X + 10;
            var html = '<div style="position:relative;padding:16px 16px 28px;border:1px solid #ddd;background:#f2ece1;border-radius:16px;">';
            html += '<div style="position:relative;overflow:hidden;width:' + w + 'px;">';
            html += '<div style="position:relative;width:' + w + 'px;height:' + h + 'px;">';
            html += '<img id="scream" src="' + imgSrc + '" style="width:' + w + 'px;height:' + h + 'px;">';
            html += '<canvas id="puzzleBox" width="' + w + '" height="' + h + '" style="position:absolute;left:0;top:0;z-index:22;">您的浏览器不支持此标签</canvas>';
            html += '</div>';
            html += '<div class="puzzle-lost-box" style="position:absolute;width:' + w + 'px;height:' + h + 'px;top:0;left:' + left_Num + 'px;z-index:111;">';
            html += '<canvas id="puzzleShadow" width="' + w + '" height="' + h + '" style="position:absolute;left:0;top:0;z-index:22;"></canvas>';
            html += '<canvas id="puzzleLost" width="' + w + '" height="' + h + '" style="position:absolute;left:0;top:0;z-index:33;"></canvas>';
            html += '</div>';
            html += '<p class="ver-tips"></p>';
            html += '</div>';
            html += '<div class="re-btn" style="width:20px; height:20px;"><a> </a></div>';
            html += '</div>';
            html += '<br>';
            html += '<div style="position:relative;width:' + w + 'px;margin:auto;">';
            html += '<div style="border:1px solid #c3c3c3;border-radius:24px;background:#ece4dd;box-shadow:0 1px 1px rgba(12,10,10,0.2) inset;">';
            html += '<p style="font-size:12px;color: #486c80;line-height:28px;margin:0;text-align:right;padding-right:22px;">按住左边滑块，拖动完成上方拼图</p>';
            html += '</div>';
            html += '<div class="slider-btn" style="width:40px; height:40px; "> </div>';
            html += '</div>';
            el.html(html);
            var d = PL_Size / 3;
            var c = document.getElementById("puzzleBox");
            var ctx = c.getContext("2d");
            ctx.globalCompositeOperation = "xor";
            ctx.shadowBlur = 10;
            ctx.shadowColor = "#fff";
            ctx.shadowOffsetX = 3;
            ctx.shadowOffsetY = 3;
            ctx.fillStyle = "rgba(0,0,0,0.7)";
            ctx.beginPath();
            ctx.lineWidth = "1";
            ctx.strokeStyle = "rgba(0,0,0,0)";
            ctx.moveTo(X, Y);
            ctx.lineTo(X + d, Y);
            ctx.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);
            ctx.lineTo(X + 3 * d, Y);
            ctx.lineTo(X + 3 * d, Y + d);
            ctx.bezierCurveTo(X + 2 * d, Y + d, X + 2 * d, Y + 2 * d, X + 3 * d, Y + 2 * d);
            ctx.lineTo(X + 3 * d, Y + 3 * d);
            ctx.lineTo(X, Y + 3 * d);
            ctx.closePath();
            ctx.stroke();
            ctx.fill();
            var c_l = document.getElementById("puzzleLost");
            var c_s = document.getElementById("puzzleShadow");
            var ctx_l = c_l.getContext("2d");
            var ctx_s = c_s.getContext("2d");
            var img = new Image();
            img.src = imgSrc;
            img.onload = function() { ctx_l.drawImage(img, 0, 0, w, h); }
            ctx_l.beginPath();
            ctx_l.strokeStyle = "rgba(0,0,0,0)";
            ctx_l.moveTo(X, Y);
            ctx_l.lineTo(X + d, Y);
            ctx_l.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);
            ctx_l.lineTo(X + 3 * d, Y);
            ctx_l.lineTo(X + 3 * d, Y + d);
            ctx_l.bezierCurveTo(X + 2 * d, Y + d, X + 2 * d, Y + 2 * d, X + 3 * d, Y + 2 * d);
            ctx_l.lineTo(X + 3 * d, Y + 3 * d);
            ctx_l.lineTo(X, Y + 3 * d);
            ctx_l.closePath();
            ctx_l.stroke();
            ctx_l.shadowBlur = 10;
            ctx_l.shadowColor = "black";
            ctx_l.clip();
            ctx_s.beginPath();
            ctx_s.lineWidth = "1";
            ctx_s.strokeStyle = "rgba(0,0,0,0)";
            ctx_s.moveTo(X, Y);
            ctx_s.lineTo(X + d, Y);
            ctx_s.bezierCurveTo(X + d, Y - d, X + 2 * d, Y - d, X + 2 * d, Y);
            ctx_s.lineTo(X + 3 * d, Y);
            ctx_s.lineTo(X + 3 * d, Y + d);
            ctx_s.bezierCurveTo(X + 2 * d, Y + d, X + 2 * d, Y + 2 * d, X + 3 * d, Y + 2 * d);
            ctx_s.lineTo(X + 3 * d, Y + 3 * d);
            ctx_s.lineTo(X, Y + 3 * d);
            ctx_s.closePath();
            ctx_s.stroke();
            ctx_s.shadowBlur = 20;
            ctx_s.shadowColor = "black";
            ctx_s.fill();
            var moveStart = '';

            $(".slider-btn").mousedown(function(e) {

                e = e || window.event;
                $(this).css({ "background-position": "0 -216px" });
                moveStart = e.pageX;
            });
            onmousemove = function(e) {
                e = e || window.event;
                var moveX = e.pageX;
                var d = moveX - moveStart;
                if (moveStart == '') {

                } else {
                    if (d < 0 || d > (w - padding - PL_Size)) {

                    } else {
                        $(".slider-btn").css({ "left": d + 'px', "transition": "inherit" });
                        $("#puzzleLost").css({ "left": d + 'px', "transition": "inherit" });
                        $("#puzzleShadow").css({ "left": d + 'px', "transition": "inherit" });
                    }
                }
            };
            onmouseup = function(e) {
                e = e || window.event;
                var moveEnd_X = e.pageX - moveStart;
                var ver_Num = X - 10;
                var deviation = 4;
                var Min_left = ver_Num - deviation;
                var Max_left = ver_Num + deviation;
                if (moveStart == '') {

                } else {
                    if (Max_left > moveEnd_X && moveEnd_X > Min_left) {
                        $(".ver-tips").html('<i style="background-position:-4px -1207px;"></i><span style="color:#42ca6b;">验证通过</span><span></span>');
                        $(".ver-tips").addClass("slider-tips");
                        $(".puzzle-lost-box").addClass("hidden");
                        $("#puzzleBox").addClass("hidden");
                        setTimeout(function() {
                            $(".ver-tips").removeClass("slider-tips");
                            imgVer(Config);
                        }, 2000);
                        Config.success();
                    } else {
                        $(".ver-tips").html('<i style="background-position:-4px -1229px;"></i><span style="color:red;">验证失败:</span><span style="margin-left:4px;">拖动滑块将悬浮图像正确拼合</span>');
                        $(".ver-tips").addClass("slider-tips");
                        setTimeout(function() {
                            $(".ver-tips").removeClass("slider-tips");
                        }, 2000);
                        Config.error();
                    }
                }
                setTimeout(function() {
                    $(".slider-btn").css({ "left": '0', "transition": "left 0.5s" });
                    $("#puzzleLost").css({ "left": '0', "transition": "left 0.5s" });
                    $("#puzzleShadow").css({ "left": '0', "transition": "left 0.5s" });
                }, 1000);
                $(".slider-btn").css({ "background-position": "0 -84px" });
                moveStart = '';
                $(".re-btn a").on("click", function() {
                    imgVer(Config);
                });
            }
        }



        imgVer({
            el: '$("#imgVer")',
            width: '260',
            height: '116',
            img: [
                require('@/assets/image/verify/ver-1.png'),
                require('@/assets/image/verify/ver-1.png'),
                require('@/assets/image/verify/ver-2.png'),
                require('@/assets/image/verify/ver-3.png')
            ],
            success: this.AdminLogin,
            error: function() {
                console.log("拼图滑块验证失败！");
            }
        });



        // $(".submit").on('click', function() {
        //     if ($(".username").val() == '') {
                
        //     } else if ($(".password").val() == '') {
        //         $(".tips").html("<span style='color:red;'>老兄 ！！你密码呢 ！？？</span>");
                
        //         $("#logo").attr("src",require('@/assets/image/verify//null-password.jpg'));
        //     } else {
        //         $(".login").css({
        //             "left": "-404px",
        //             "opacity": "0"
        //         });
        //         $(".verBox").css({
        //             "left": "0",
        //             "opacity": "1"
        //         })
        //     }
        // });

    },
    watch: {
      // 如果 `clientHeight` 发生改变，这个函数就会运行
      clientHeight: function () {
         this.changeFixed(this.clientHeight)
      }
    },
    methods:{
        changeFixed(clientHeight){ //动态修改样式
            console.log(clientHeight);
            this.$refs.homePage.style.height = (clientHeight-1)+'px';
        },
        // 登录
        verify(){
            if(this.username == ''){
                // 用户名为空
                $(".tips").html("<span style='color:red;'>老兄 ！！你用户名呢！？？</span>");
                $("#logo").attr("src", require('@/assets/image/verify/null-password.jpg'));  
            } else if (this.password == '') {
                // 密码为空
                $(".tips").html("<span style='color:red;'>老兄 ！！你密码呢 ！？？</span>");
                $("#logo").attr("src",require('@/assets/image/verify//null-password.jpg'));
            } else {
                $(".login").css({
                    "left": "-404px",
                    "opacity": "0"
                });
                $(".verBox").css({
                    "left": "0",
                    "opacity": "1"
                })
            }
        },
        // 请求
        AdminLogin(){
            // axios请求
            let params = this.$qs.stringify({
                username : this.username,
                password : this.password
            });
            this.$axios.post("http://www.wxhwg.com:8080/mock/admin/login",params)
            .then(response=>{
                let result =response.data;
                if(result.status == "200"){
                    // 登录成功，存入sessionStorage
                    this.$store.dispatch("loginAdmin",result.data.username);

                    this.$message.success("管理员登录成功！");
                    // 隐藏拼图模块
                    $(".login").css({
                        "left": "0",
                        "opacity": "1"
                    });
                    $(".verBox").css({
                        "left": "404px",
                        "opacity": "0"
                    });

                    // 跳转页面
                    this.$router.replace({path:'/admininterface'});
                }else{
                    this.$message.warning("用户名或密码错误！");
                    // 隐藏拼图模块
                    $(".login").css({
                        "left": "0",
                        "opacity": "1"
                    });
                    $(".verBox").css({
                        "left": "404px",
                        "opacity": "0"
                    });
                    // 原登录页面
                    $(".tips").html("<span style='color:red;'>你是不是不知道账号密码 ！？？</span>");
                    $("#logo").attr("src", require('@/assets/image/verify/login-err.png'));
                    
                }
            }).catch(error=>{
                alert(error);
            });

        }


    }
}
</script>


<style >
    /* 加scoped导致资源图片无法加载，需要使用<%basepath处理？%> */

    .adminlogin .el-col{
        min-height: 1em;
    }
    .adminlogin{
        /* background: transparent url(~@/assets/image/bg.jpg) no-repeat fixed;
        background-size: cover; */
    }
    .adminlogin .cover {
            width: 100%;
            height: 100%;
            position: relative;
            color: #fff;
        }
    .adminlogin .cover::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width:  100%;
        height: 100%;
        background: transparent url(~@/assets/image/bg.jpg) no-repeat fixed;
        filter: blur(100px);
        z-index: -1;
        background-size: cover;
    }

    .adminlogin .box-card{
        min-height: 400px;
        width: 400px;
    }

    .maincotent{
        position:fixed;
        width: 400px;
        height: 400px;
        left: 0;
        right:0;
        top: 0;
        bottom: 0;
        margin:auto;
    }


     .bxs-row {
        margin-bottom: 12px;
    }
    





    /* 验证 */
    .logo-box {
        background-color: rgba(255,255,255,0.6);
        width: 404px;
        border: 1px solid #e5e5e5;
        border-radius: 4px;
        box-shadow: 0 4px 18px rgba(0, 0, 0, 0.2);
        position: relative;
        overflow: hidden;
        height: 360px;
    }
    
    .adminlogin .login {
        position: absolute;
        width: 320px;
        left: 0;
        top: 0;
        padding: 42px 42px 36px;
        transition: all 0.8s;
    }
    
    .adminlogin .username,
    .adminlogin .password,
    .adminlogin .btn {
        height: 44px;
        width: 100%;
        padding: 0 10px;
        border: 1px solid #9da3a6;
        background: #fff;
        text-overflow: ellipsis;
        box-sizing: border-box;
        border-radius: 4px;
        box-shadow: none;
        color: #000;
        font-size: 1em;
        font-weight: 400;
        direction: ltr;
        font-size: 13px;
    }
    
    .adminlogin .submit {
        background-color: #0070ba;
        color: #fff;
        border: 1px solid #0070ba;
    }
    
    .adminlogin .submit:hover {
        background-color: #005ea6;
    }
    
    .verBox {
        position: absolute;
        width: 100%;
        text-align: center;
        left: 404px;
        top: 0;
        opacity: 0;
        transition: all 0.8s;
        padding-top: 55px;
    }
    
    .err {
        margin: 12px 0 0;
        line-height: 12px;
        height: 12px;
        font-size: 12px;
        color: red;
    }

    .slider-btn {
        position: absolute;
        width: 44px;
        height: 44px;
        left: 0;
        top: -7px;
        z-index: 12;
        cursor: pointer;
        background-image: url('~@/assets/image/verify/sprite.3.2.0.png');
        background-position: 0 -84px;
        transition: inherit
    }

.ver-tips {
    position: absolute;
    left: 0;
    bottom: -22px;
    background: rgba(255, 255, 255, .9);
    height: 22px;
    line-height: 22px;
    font-size: 12px;
    width: 100%;
    margin: 0;
    text-align: left;
    padding: 0 8px;
    transition: all .4s
}

.slider-tips {
    bottom: 0
}

.ver-tips i {
    display: inline-block;
    width: 22px;
    height: 22px;
    vertical-align: top;
    background-image: url('~@/assets/image/verify/sprite.3.2.0.png');
    background-position: -4px -1229px
}

.ver-tips span {
    display: inline-block;
    vertical-align: top;
    line-height: 22px;
    color: #455
}

.active-tips {
    display: block
}

.hidden {
    display: none
}

.re-btn {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 28px;
    padding: 0 16px
}

.re-btn a {
    display: inline-block;
    color: tomato;
    width: 14px;
    height: 14px;
    margin: 7px 0;
    background-image: url('~@/assets/image/verify/sprite.3.2.0.png');
    background-position: 0 -1179px;
    cursor: pointer
}

.re-btn a:hover {
    background-position: 0 -1193px
}
</style>

