<template>
<div id="fromUp">
    <el-dialog width="32%" title="添加商品图片" :visible.sync="show">
        <div class="demo-image"  >
            <div class="block"  >   
                <el-image v-for="fits in photolist" :key="fits.productId"
                style="width: 80px; height: 80px"
                :src="(path)+fits.productPhoto"
                @click="click"
                :title="fits.productPhoto"
                fit="contain"></el-image>
            </div>
        </div>
    </el-dialog>

    <!-- 添加商品>上传图片 -->
    <el-dialog width="32%" title="上传图片" :visible.sync="show1">
        <i-upload @messageData="getData"></i-upload>
    </el-dialog>

  <div style="width:40%">
  <el-form status-icon :rules="rules" ref="fromList" :model="fromList">
    <el-form-item label="产品序列号" :label-width="formLabelWidth" prop="productCode">
      <el-input v-model="fromList.productCode" autocomplete="off"></el-input>
    </el-form-item>
   <el-form-item label="商品名称" :label-width="formLabelWidth" prop="productName">
      <el-input v-model="fromList.productName" autocomplete="off"></el-input>
    </el-form-item>
	<el-form-item label="规  格" :label-width="formLabelWidth" prop="productStandard">
      <el-select v-model="fromList.productStandard"  placeholder="请选择规格">
      <el-option label="袋" value="袋"></el-option>
      <el-option label="瓶" value="瓶"></el-option>
      <el-option label="包" value="包"></el-option>
    </el-select>
    </el-form-item>
	<el-form-item label="小单位" :label-width="formLabelWidth" prop="productSmallUnit">
      <el-input v-model="fromList.productSmallUnit" autocomplete="off"></el-input>
    </el-form-item>
	<el-form-item prpo="price" label="小单位价格" :label-width="formLabelWidth" prop="productSmallPrice">
      <el-input type="price" v-model="fromList.productSmallPrice" autocomplete="off"></el-input>
    </el-form-item>
		<el-form-item label="大单位规格" :label-width="formLabelWidth" prop="productLargerStandard"> 
       <el-select v-model="fromList.productLargerStandard" placeholder="请选择规格">
      <el-option label="条" value="条"></el-option>
      <el-option label="箱" value="箱"></el-option>
      <el-option label="盒" value="盒"></el-option>
      <el-option label="件" value="件"></el-option>
    </el-select>
    </el-form-item>
	<el-form-item label="大单位" :label-width="formLabelWidth" prop="productLargerUnit">
      <el-input v-model="fromList.productLargerUnit" autocomplete="off"></el-input>
    </el-form-item>
	<el-form-item label="大单位价格" :label-width="formLabelWidth" prop="productLargerPrice">
      <el-input v-model="fromList.productLargerPrice" autocomplete="off"></el-input>
    </el-form-item>
	<el-form-item label="类别编号" :label-width="formLabelWidth" prop="productCategoryId">
   <el-select v-model="fromList.productCategoryId" placeholder="请选择类别">
      <el-option v-for=" cate in cateList" :key="cate.productCategoryId" :label="cate.categoryName" :value="cate.categoryId"></el-option>
    </el-select>
    </el-form-item>
	<el-form-item label="品  牌" :label-width="formLabelWidth" prop="productBrandId">
      <el-input v-model="fromList.productBrandId" autocomplete="off"></el-input>
    </el-form-item>
	<el-form-item label="图片名称" :label-width="formLabelWidth" prop="productPhoto">
      <el-input style="" v-model="fromList.productPhoto" autocomplete="off">  
         <el-button 	 @click="addPhoto" slot="append" icon="el-icon-plus"></el-button>
         <el-button @click="uploadPhoto" slot="append" icon="el-icon-upload"></el-button>
   </el-input> 
    </el-form-item>
	<el-form-item label="是否在销" :label-width="formLabelWidth" prop="productIsSale">
          <el-radio-group v-model="fromList.productIsSale">
      <el-radio :label="0" border>是</el-radio>
      <el-radio :label="1" border>否</el-radio>
    </el-radio-group>
    </el-form-item>
	<el-form-item label="缺货状态" :label-width="formLabelWidth" prop="productIsLack">
      <el-radio-group v-model="fromList.productIsLack">
      <el-radio :label="0" border>有货</el-radio>
      <el-radio :label="1" border>缺货</el-radio>
    </el-radio-group>
    </el-form-item>
	<el-form-item label="状  态" :label-width="formLabelWidth" prop="productStatus">
       <el-radio-group v-model="fromList.productStatus">
      <el-radio :label="0" border>有效</el-radio>
      <el-radio :label="1" border>无效</el-radio>
    </el-radio-group>
    </el-form-item>
	<el-form-item label="排  序" :label-width="formLabelWidth" prop="productOrder">
      <el-input v-model="fromList.productOrder" autocomplete="off"></el-input>
    </el-form-item>
		<el-form-item label="建议零售价" :label-width="formLabelWidth" prop="productSuggestPrice">
      <el-input v-model="fromList.productSuggestPrice" autocomplete="off"></el-input>
          </el-form-item>
      <el-form-item :label-width="formLabelWidth">
          <el-button @click="que('fromList')">取 消</el-button>
          <el-button type="primary" @click="fromUp('fromList')">确 定</el-button>
      </el-form-item>
  </el-form>
  </div>

    </div>
</template>
<script>
import upload from './upload.vue' // 
export default {
  components: {
    "i-upload":upload,
  },
  props:['transmits'],
    data(){
        // 排序验证
        var numberNull=(rule,value,callback)=>{
          if(!value){
            return callback(new Error('不能为空 值为0-5 值越小商品越靠前'));
          }
          else if(/(^[1-9]\d*$)/.test(value)) {
            if(5>=value && value>=0){
            callback();
            }
            else{
             callback(new Error("数值范围为 0-5"));
            }
          }else {
            callback(new Error("请输入正整数"));
          }
        } 
        //照片非空验证
        var photoNull=(rule,value,callback)=>{
          if(!value){
            return callback(new Error("不能为空"))
          }
          else{
            callback();
          }
        }
        // 按钮非空验证
        var btnNull=(rule,value,callback)=>{
           if(value==null){
            return callback(new Error('请选择状态'));
          }
          else{
            callback();
          }
        }
        // 下拉选取验证
        var chaneNull=(rule ,value,callback)=>{
          if(!value){
            return callback(new Error('请选择类型'));
          }
          else{
             callback();
          }
        } 
        // 价格非负 非空验证
        var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('价格不能为空'));
        }
        setTimeout(() => {
          if (/^[0-9]+.?[0-9]*$/.test(value)) {
            callback();
          }else{
            callback(new Error('只能输入正数'));
          }
        }, 1000);
      };
        return{
          show:false,
          show1:false,
          fromStatus:'add',
          photolist:[],
          cateList:[],
          path:'http://www.wxhwg.com:8080/mock/static/',
        fromList:{
                productCode:'',
                productName:'',
                productStandard:'', 
                productSmallUnit:'',
                productSmallPrice:'',
                productLargerStandard:'',
                productLargerUnit:'',
                productLargerPrice:'',
                productCategoryId:'',
                productBrandId:'',
                productPhoto:'',
                productIsSale:'',
                productIsLack:'',
                productStatus:'',
                productOrder:'',
                productSuggestPrice:''
            },
           formLabelWidth: '120px',
           rules:{
           productLargerStandard: [
            { validator: chaneNull, message: '请选择大单位规格', trigger: 'change' }
          ],
          productStandard: [
            { validator: chaneNull, message: '请选择小单位规格', trigger: 'change' }
          ],
          productIsSale: [
            { validator:btnNull , trigger: 'change' }
          ],
           productStatus: [
            { validator:btnNull ,  trigger: 'change' }
          ],
           productIsLack: [
            { validator: btnNull, trigger: 'change' }
          ],
          productSmallPrice:[
            {validator: checkAge, trigger: 'blur' }
          ],
          productLargerPrice:[
            {validator: checkAge, trigger: 'blur' } 
          ],
          productSuggestPrice:[
             {validator: checkAge, trigger: 'blur' } 
          ],
          productOrder:[
            {validator: numberNull,  trigger: 'blur'}
          ],
         productCode :[
          {validator: photoNull,  trigger: 'blur'}          
            ],
            productCategoryId:[ {validator: photoNull,  trigger: 'blur'} ],
           productName :[
          {validator: photoNull,  trigger: 'blur'}          
            ],
           productStandard  :[
          {validator: photoNull,  trigger: 'blur'}          
            ],
           productSmallUnit  :[
          {validator: photoNull,  trigger: 'blur'}          
            ],
            productLargerStandard :[
          {validator: photoNull,  trigger: 'blur'}          
            ],
            productLargerUnit:[
          {validator: photoNull,  trigger: 'blur'}          
            ],
            productPhoto:[ {validator: photoNull,  trigger: 'blur'}],
            productBrandId:[
          {validator: photoNull,  trigger: 'blur'}          
            ],
           }
        }
    },
    created(){
      this.newinit();
        this.init();
     },
    methods:{
    newinit(){
      if(this.transmits){
        //this.fromList=this.transmits;
        this.fromList.productCode=this.transmits.productCode;
        this.fromList.productName=this.transmits.productName;
        this.fromList.productStandard=this.transmits.productStandard;
        this.fromList.productSmallUnit=this.transmits.productSmallUnit;
        this.fromList.productSmallPrice=this.transmits.productSmallPrice;
        this.fromList.productLargerStandard=this.transmits.productLargerStandard;
        this.fromList.productLargerUnit=this.transmits.productLargerUnit;
        this.fromList.productLargerPrice=this.transmits.productLargerPrice;
        this.fromList.productCategoryId=this.transmits.productCategoryId;
        this.fromList.productBrandId=this.transmits.productBrandId;
        this.fromList.productPhoto=this.transmits.productPhoto;
        this.fromList.productIsSale=this.transmits.productIsSale;
        this.fromList.productIsLack=this.transmits.productIsLack;
        this.fromList.productStatus=this.transmits.productStatus;
        this.fromList.productOrder=this.transmits.productOrder;
        this.fromList.productSuggestPrice=this.transmits.productSuggestPrice;
        this.fromStatus='update';
      }else{

      }
    },
      que(fromList){
           this.$refs[fromList].resetFields();
           this.$emit('messageData','')
      },
      init(){
         this.$axios({
              method:'get',
              url:'http://www.wxhwg.com:8080/mock/product/list',
              params:{
                categoryId:'',
                pageNum:1,
                pageSize:100,
                productName:'',
                isLack:''
              }
          }).then(response=>{
              this.$message({
                message:response.data.message,
                center:true,
                duration:1000,
                type:1000,
              }),             
              this.photolist=response.data.data.list;
          })
          this.$axios({
            method:'get',
            url:'http://www.wxhwg.com:8080/mock/category/qryList'
          }).then(response=>{
            this.$message({
                message:response.data.message,
                center:true,
                duration:1000,
                type:1000,
              }),
            this.cateList=response.data.data;
          })
      },
      click(){
        this.fromList.productPhoto=(event.target.title);
        this.show=false;
      },
    // 新增商品
    fromUp(fromList){
        let self=this;
        this.dialogFormVisible = false
       this.$refs[fromList].validate((valid) => {
          if (valid) {
         this.$axios({
         method:'POST',
         url:'http://wxhwg.com:8080/mock/product/'+self.fromStatus,
         data:this.$qs.stringify(self.fromList)
       }).then(response=>{
         if(response.status==200){
         this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
        this.$emit('messageData','')
         }else {
             this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
         }
       }).catch(err=>{
         this.$message({
           message:'提交失败',
           center:true,
           duration:1000,
           type:1000,
         })
       })
          } else {
            return false;
          }
           });
     },
    addPhoto(){
      this.show=true
         },
     uploadPhoto(){
     this.show1=true
     },
     getData(val){
       let self=this;
       this.show1=false;
       if(val){
         self.fromList.productPhoto=val;
       }
     }
    }
}
</script>

<style scoped>
#fromUp input::-webkit-outer-spin-button,
#fromUp input::-webkit-inner-spin-button {
    -webkit-appearance: none ;
    appearance: none ; 
    margin: 0 ; 
}
</style>