<template>
<div>
 <el-select
    v-model="value"
    multiple
    :filterable="bool"
    :remote="bool"
    reserve-keyword
    placeholder="请输入关键词"
    :remote-method="remoteMethod"
    :loading="loading">
  </el-select> 
<!-- <template>
    <i-showData v-if="bbll" v-bind:title="idData"></i-showData>
</template> -->
</div>
</template>
<script>
export default {
    data(){
        return{
            loading:false,
            bool:true,
            bbll:false,
            options: [],
            value: [],
            list: [],
            idData:[],
            cateData:[]
        }
    },
    methods:{
       remoteMethod(query){
           let self=this;
           if(query!==''){  
               self.$axios({
                   method:'get',
                   url:'http://www.wxhwg.com:8080/mock/product/find',
                   params:{
                       productId:query
                   }
               }).then(response=>{
                   self.idData=response.data.data;
               this.$emit('messageData',response.data.data)
               })
           }
       },
    }
}
</script>

