<template>
    <div class="navgationbar">
        <el-row>
            <el-col :span="3"></el-col>
            <el-col :span="18" >
                <el-row class="navgat"  type="flex" align="middle">
                    <el-col :span="10">
                        <span @click="$router.push({path:'/index'});">首页</span>
                        <el-divider direction="vertical"></el-divider>
                        <span>与其感慨路难行</span>
                        <span>不如马上出发</span>
                    </el-col>
                    <el-col :span="14">
                        <div class="shopcar fright" @click="$router.push({path:'/shopcar'});">
                            <i class="el-icon-shopping-cart-2"></i>
                            购物车
                            (<i>0</i>)
                        </div>
                        <div class="fright" >
                            <template v-if="this.loginUser">
                                <span>
                                    <el-dropdown>
                                        <div class="el-dropdown-link">
                                            <div style="display: inline-block;">{{ this.loginUser.userUserName }}</div>
                                            <i class="el-icon-arrow-down el-icon--right"></i>
                                        </div>
                                        <el-dropdown-menu slot="dropdown">
                                            <el-dropdown-item @click.native="ShowUserInfo();">个人信息</el-dropdown-item>
                                            <el-dropdown-item @click.native="SwitchUser(); ">切换账号</el-dropdown-item>
                                            <el-dropdown-item @click.native="LogoutUser(); $router.go(0);">退出登录</el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </span>
                            </template>
                            <template v-else> 
                                <span @click="$router.push({path:'/login'});">登录</span>
                                <el-divider direction="vertical"></el-divider>
                                <span @click="$router.push({path:'/register'});">注册</span>
                            </template>
                            
                            <el-divider direction="vertical"></el-divider>
                            <span>消息通知</span>
                        </div>
                        
                    </el-col>
                </el-row>
            </el-col>
            <el-col :span="3"></el-col>
        </el-row>
    </div>
</template>



<script>
  export default {
      data(){
          return {
              loginUser : JSON.parse(this.$store.state.loginUser)
          }
      },
      created(){
          
      },
      methods : {
            LogoutUser(){
                // 退出登录，清除sessionStorage
                this.$store.dispatch("logoutUser");
                // 刷新导航栏显示
            },
            SwitchUser(){
                this.LogoutUser();
                // 跳转登录页面
                this.$router.push({path : '/login'});
            },
            ShowUserInfo(){
                this.$router.push({name : 'personalinfo',params : {userId : this.loginUser.userId}});
            }
      }
  }
</script>


<style scoped>
    *:hover{
        cursor: pointer;
    }
    .navgationbar{
        height: 40px;
        font-size: 13px;
        background-color: #e4e4e0;
        color: #666;
    }
    .navgationbar span{
        margin: 0 10px;
    }
    .navgationbar .el-divider--vertical{
        /* 最后一个竖线显示双倍宽度 */
        width: 0.5px;
        margin: 0;
        background-color: #666;
    }

    .navgat span:hover{
        color: #8c222c;
        border-bottom:1px solid #8c222c;
    }

    .navgationbar .el-col{
        height: 40px;
        line-height: 40px; 
    }
    .shopcar{
        background-color:#f0f0f0;
        height:40px;
        width:120px;
        text-align:center
    }
     .shopcar:hover{
        background-color:tomato;
        color: #fff
    }

    
</style>

