<template>
    <div class="addcategory">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
          <span>类别添加</span>
          
          </div>
              <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-ruleForm">
                  <el-form-item
                    label="类别名称"
                    prop="categoryName"
                    :rules="[
                      { required: true, message: '类别不能为空'}
                    ]"
                  >
                      <el-input  v-model=" dynamicValidateForm.categoryName" autocomplete="off" style="width:140px;float:left"></el-input>
                  </el-form-item>
                  <el-form-item label="排序">
                    <el-input-number v-model="dynamicValidateForm.num" controls-position="right" @change="handleChange" :min="1" :max="100"></el-input-number>
                  </el-form-item>
                  <el-form-item label="状态">
                      <el-switch
                    v-model="dynamicValidateForm.value"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    >
                    </el-switch>
                  </el-form-item>
                  
                  <el-form-item>
                      <el-button type="primary" @click="submitForm('dynamicValidateForm');add()">添加</el-button>
                      <el-button @click="resetForm('dynamicValidateForm')">取消</el-button>
                  </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>
<script>
export default {
     data() {
         
      return {
        dynamicValidateForm: {
          categoryName: '',
          categoryOrder:'',
          categoryStatus:'',
           num: 1,
           value: true
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
           // this.$alert('添加成功!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
     add(){
          this.$axios({
            method:'post',
            url:'http://www.wxhwg.com:8080/mock/category/add',
            data:this.$qs.stringify({
                  categoryName : this.dynamicValidateForm.categoryName,
                  categoryOrder : this.dynamicValidateForm.num,
                  categoryStatus : this.value
              })
            }).then(function (response) {
                var result=response.data;
                if(result.status=200){
                   
                alert('添加成功')

                }else{
                alert(result.message)
                
                }
              
                }).catch(function (error) {
                  console.log(error)
                })
      },
       handleChange(value) {
        console.log(value);
      }
    }
  
    
}
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .addcategory .box-card {
    width: 600px;
    height: 500px;
    margin: 0 auto;
    margin-top: 20px
  }
</style>


