<template>
<div class="productsManage">
    <i-test1 @messageData="getData"></i-test1>
    <i-showdata  v-bind:title="tableData"></i-showdata>
    <div class="block">
        <el-pagination
          @size-change="sizeChange"
          @current-change="pageNumChange"
          :current-page="pageNum"
          :page-sizes="[10,20,30,40]"
          :hide-on-single-page="bool"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </div>
 </div>
</template>
<script>
// 查询搜索框
import test1 from './test1.vue'
// 商品展示
import showData from './showData.vue'
export default {
  components: {
    "i-test1":test1,
    "i-showdata":showData,
  },
    name:'productsManage',
    data(){
        return{
          status:'',
          status1:'',
          bool:false,
          tableData:[],
          path:'http://www.wxhwg.com:8080/mock/static/',
          pageNum:1,
          pageSize:16,
          total:0,
        }
    },
    created(){
        this.init();
  },
      methods:{
        // 初始化数据源
      init(){
        let self=this;
          this.$axios({
              method:'get',
              url:'http://www.wxhwg.com:8080/mock/product/list',
              params:{
                categoryId:'',//02c6fd20-808b-441e-b02c-edb5b7e95df1
                pageNum:self.pageNum,
                pageSize:self.pageSize,
                productName:'',
                isLack:''
              }
          }).then(response=>{
            self.tableData=response.data.data.list;
            self.total=response.data.data.total;
           this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         }) 
          })
      },
      sizeChange(val){
        this.pageSize=val;
        this.init();
      },
      pageNumChange(val){
        this.pageNum=val;
        this.init();
      },
      getData(val){
        this.tableData=[];
        this.tableData = this.tableData.concat([val])
                  this.total=1;
      },
  },
}
</script>



<style scoped>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>
