<template>
    <div class="showuser">
        <el-col>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="用户名" prop="userUserName">
                <el-input v-model="ruleForm.userUserName"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="userPassword" show-password>
                <el-input v-model="ruleForm.userPassword"></el-input>
            </el-form-item>
            <el-form-item label="电话" prop="userTelphone">
                <el-input maxlength="11" v-model="ruleForm.userTelphone"></el-input>
            </el-form-item>
            <el-form-item label="商店名称" prop="userShopName">
                <el-input v-model="ruleForm.userShopName"></el-input>
            </el-form-item>
            <el-form-item label="地址" prop="userAddress">
                <el-input v-model="ruleForm.userAddress"></el-input>
            </el-form-item>
            <el-form-item label="状态">
                <el-switch v-model="ruleForm.userStatus" active-text="开启" inactive-text="停用" active-value="1" inactive-value="0"></el-switch>
            </el-form-item>
            <el-form-item label="备注" prop="userComment">
                <el-input type="textarea" v-model="ruleForm.userComment" :autosize="{ minRows: 4, maxRows: 5}"></el-input>
            </el-form-item>
            </el-form>
        </el-col>
    </div>
</template>


<script>
  export default {
    name : "ShowUser",
    data() {
        return {
            ruleForm: {
                userId : '',
                userUserName : '',
                userPassword : '',
                userTelphone : '',
                userAddress : '',
                userShopName : '',
                userComment : '',
                userStatus : '1'
            },
            rules: {
                userUserName : [{
                        required: true,         // 是否必填
                        message: '请输入用户名', // 不符合提示
                        trigger: 'blur'         // 点击别处触发
                    },
                    {
                        min: 2,                 // 最长字符或数字
                        max: 10,                 // 最短字符或数字
                        message: '长度在 2 到 10 个字符', 
                        trigger: 'change' 
                    }
                ],
                userPassword : [{
                        required: true,
                        message: '请输入密码',
                        trigger: 'blur' 
                    }
                    ,
                    {
                        pattern: /^\w{3,16}$/,    // 正则表达式
                        message: '只能3~16个数字、字母、下划线组成',
                        trigger: 'change' 
                    }
                ],
                userTelphone: [{
                        required: true,
                        pattern: /^1[34578]\d{9}$/, 
                        message: '请输入正确的电话号码', 
                        trigger: 'change' 
                    }
                ],
                userShopName: [{
                        required: true,
                        message: '请输入商店名称',
                        trigger: 'blur' 
                    }
                ],
                userAddress:[{
                    required: true,
                    message: '请输入地址',
                    trigger: 'blur' 
                }],
                userComment:[{
                    required: true,
                    message: '请输入备注',
                    trigger: 'blur' 
                }]

            }
        };
    },
    methods: {
        refreshFormInfo (userData) {
            console.log("组件：");
            console.log(userData.userId);
            this.ruleForm.userId = userData.userId;
            this.ruleForm.userUserName = userData.userUserName;
            this.ruleForm.userPassword = userData.userPassword;
            this.ruleForm.userTelphone = userData.userTelphone;
            this.ruleForm.userAddress = userData.userAddress;
            this.ruleForm.userShopName = userData.userShopName;
            this.ruleForm.userComment = userData.userComment;
            this.ruleForm.userStatus = userData.userStatus;
        },
        getForm(){
            console.log(this.$refs['ruleForm']);
            return this.$refs.ruleForm;
        },
        getFormData(){
            return this.ruleForm;
        }
    }
  }
</script>