<template>
  <div class="productdetail">
    <i-nav></i-nav>
    <el-row :gutter="10">
      <!--当前所在位置-->
      <el-col :span="14" :offset="6">
        <div class="bg-purple-bottom">
          <!-- 当前所在位置 -->
        </div>
      </el-col>
    </el-row>

    <!-- 第二行 -->
    <el-row>
      <!--大图片-->
      <el-col :span="6" style="min-height:1em;"></el-col>
      <el-col :span="6">
        <div style="width: 300px; height: 300px;  margin:0 auto; text-align:center">
          <!-- 使用el-image存在问题 -->
          <img style="width: 290px; height: 290px; margin:0 auto" :src="web_static_server+photo">
        </div>
      </el-col>
      <!--测试-->
      <el-col :span="8">
        <div class="grid-content bg-purple" style="min-height:50px;text-align: left">
          <h1>{{example.productName}}</h1>
        </div>
      </el-col>
      <!--显示价格,信息，挑选口味，单位-->
      <el-col :span="8">
        <div class="grid-content bg-purple" style="min-height:140px align=left text-align: center">
          <ul>
            <li>生产日期：{{this.example.productDateTime}}</li>
            <li>商铺名称：{{this.example.productBrandId}}</li>
            <li>价格：{{this.price}}￥</li>
            <li>
              <div class="tag-group">
                <span class="tag-group__title">口味：</span>
                <el-tag
                  v-for="item in items"
                  :key="item.label"
                  :type="item.type"
                  effect="dark"
                  size="small"
                  @click="choiceTeast(item.type)"
                >
                  <p>{{ item.label }}</p>
                </el-tag>
              </div>
            </li>
            <li>
              <el-row class="block-col-2">
                <el-col :span="12">
                  单位：
                  <el-dropdown trigger="click">
                    <span class="el-dropdown-link">
                      {{this.unit}}
                      <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item id="el1" icon="el-icon-plus" @click.native="choice('a')">盒</el-dropdown-item>
                      <el-dropdown-item
                        id="el2"
                        icon="el-icon-circle-plus"
                        @click.native="choice('b')"
                      >件</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </el-col>
              </el-row>
            </li>
          </ul>
        </div>
      </el-col>

      <!--选定具体商品-->
      <el-col :span="8">
        <div class="grid-content bg-purple" style="min-height:80px text-align: center">
          <ul>
            <li>
              <div style="height:50px">
                <span>选定：</span>
                {{this.example.productName}}
                <span>/</span>
                <span>{{teast}}</span>
                <span>/*{{num}}&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</span>
                <span>共计:</span>
                <span style="font-size:26px;color:red">
                  <em>{{(num*price).toFixed(2)}}</em>
                </span>
                <span>￥</span>
              </div>
            </li>
            <!-- 选定数量 -->
            <li>
              <span style="height=80px">数量：</span>
              <el-input-number :min="1" size="small" v-model="num"></el-input-number>
            </li>
          </ul>
        </div>
      </el-col>
    </el-row>

    <!-- 第三行 -->
    <!--小图片-->
    <el-row>
      <el-col :span="6" style="min-height:1px;"></el-col>
      <el-col :span="6">
        <el-carousel :interval="4000" type="card" height="60px">
          <el-carousel-item v-for="item in lunbolist" :key="item">
            <!-- 实际响应面积大于限制大小的图片 -->
            <img :src="item.src" alt style="height:60px">
          </el-carousel-item>
        </el-carousel>
      </el-col>

      <!--加入购物车-->
      <el-col :span="8">
        <div class="bg-purple-bottom" style="text-align: left">
          <div style="height:60px text-align: left">
            <el-button type="danger" style="margin-top:10px" @click.native="buyNew()">立即购买</el-button>
            <el-button
              plain="true"
              type="primary"
              style="margin-top:10px "
              @click.native="addShopCar()"
            >添加购物车</el-button>
          </div>
        </div>
      </el-col>
      <el-col :span="4" style="min-height:1px;"></el-col>
    </el-row>
  </div>
</template>

<script>
import Nav from "@/components/Common/NavgationBar.vue";
export default {
  name: "home",
  components: {
    "i-nav": Nav
  },
  data() {
    return {
      fits: "fill",
      photo: "",
      web_static_server: "http://www.wxhwg.com:8080/mock/static/",
      productId: this.$route.query.id,
      example: "",
      pictruelist: "",
      price: "0",
      maxprice: "",
      minprice: "",
      countPrice: "",
      unit: "盒",
      num: "",
      items: [
        { type: "", label: "变态辣" },
        { type: "success", label: "椒盐" },
        { type: "info", label: "麻辣" },
        { type: "danger", label: "鲜香" },
        { type: "warning", label: "五仁" }
      ],
      teast: "变态辣",
      lunbolist: [
        { src: "http://www.wxhwg.com:8080/mock/static/1555155703372.JPG" },
        { src: "http://www.wxhwg.com:8080/mock/static/1555156735685.jpg" },
        { src: "http://www.wxhwg.com:8080/mock/static/1555156735685.jpg" }
      ],

      /* 本地ID */
      userId: '',
      dialogFormVisible: false,
      inputUserName: "", //用户名
      inputPassWord: "" //用户密码
    };
  },
  created() {
    this.init();
    this.userId = JSON.parse(this.$store.state.loginUser).userId;
  },
  methods: {
    //初始化
    init() {
      // 商品信息的初始化
      this.$axios
        .get("http://www.wxhwg.com:8080/mock/api/product/find", {
          params: {
            productId: this.productId
          }
        })
        .then(response => {
          let list = response.data;
          // -----?????
          if (response.status == 200) {
            this.example = list.data;
            this.maxprice = list.data.productLargerPrice;
            this.minprice = list.data.productSmallPrice;
            this.price = this.minprice;
            this.productCategoryId = list.data.productCategoryId;
            this.photo = list.data.productPhoto;
          }
        })
        .catch(error => {
          alert(error);
        });
    },

    //判断是否登录
    loginState() {
      if (this.userId != null) {
      } else {
        this.dialogFormVisible = true;
      }
    },

    //购买单元的改变
    choice(that) {
      if (that == "a") {
        this.unit = "盒";
        this.price = this.minprice;
      } else {
        this.unit = "件";
        this.price = this.maxprice;
      }
    },

    //确定购买的口味
    choiceTeast(that) {
      switch (that) {
        case "success":
          this.teast = "椒盐";
          break;
        case "info":
          this.teast = "麻辣";
          break;
        case "danger":
          this.teast = "鲜香";
          break;
        case "warning":
          this.teast = "五仁";
          break;
        default:
          this.teast = "变态辣";
      }
    },

    //添加购物车操作
    addShopCar() {
      // 判断登录状态
      let user = JSON.parse(this.$store.state.loginUser);
      if(!user){
          var mymessage=confirm("请先登录账号！");
          if(mymessage==true){   
              this.$router.push({path:'/login'});
          }
          return;
      }



      this.$axios({
        method: "post",
        url: "http://www.wxhwg.com:8080/mock/api/detail/join",
        data: this.$qs.stringify({
          detailProductId: this.productId, // 传参
          detailProductCount: this.num,
          detailProductUnit: this.unit,
          detailProductPrice: this.price,
          detailComment: this.teast,
          detailUserId: this.userId,
          detailTotalPrice: (this.price * this.num).toFixed(2)
        })
      }).then(res => {
        let result = res.data;
        if (result.status == 200) {
          console.log(result.message);
          const h = this.$createElement;
          this.$message({
            message: h("p", null, [
              h("span", null, "添加购物车成功！！！"),
              h("i", { style: "color: teal" })
            ])
          });
        } else {
          const h = this.$createElement;
          this.$message({
            message: h("p", null, [
              h("span", null, "添加购物车失败！！！"),
              h("i", { style: "color: teal" })
            ])
          });
        }
      });
    },
    //实际添加购物车
    addShopCarSure(productId) {
      //  this.$axios({
      //         method:"post",
      //         url:"http://www.wxhwg.com:8080/mock/api/detail/join",
      //         data:this.$qs.stringify( {
      //         detailProductId:this.productId,   // 传参
      //         detailProductCount:this.num,
      //         detailProductUnit:this.unit,
      //         detailProductPrice:this.price,
      //         detailComment:this.teast,
      //         detailUserId:this.userId,
      //         detailTotalPrice:this.price*this.num
      //     })
      //     }).then((res)=>{
      //         let result=res.data
      //             if(result.status==200){
      //                   const h = this.$createElement;
      //                   this.$message({
      //                   message: h('p', null, [
      //                   h('span', null, '添加购物车成功！！！'),
      //                   h('i', { style: 'color: teal' })
      //                 ])
      //               });
      //             }else{
      //                   const h = this.$createElement;
      //                   this.$message({
      //                   message: h('p', null, [
      //                   h('span', null, '添加购物车失败！！！'),
      //                   h('i', { style: 'color: teal' })
      //                 ])
      //               });
      //             }
      //         })
    },
    //立即购买
    buyNew() {
      // 判断登录状态
      let user = JSON.parse(this.$store.state.loginUser);
      if(!user){
          var mymessage=confirm("请先登录账号！");
          if(mymessage==true){   
              this.$router.push({path:'/login'});
          }
          return;
      }


      let self = this;
      this.$axios({
        method: "post",
        url: "http://www.wxhwg.com:8080/mock/api/detail/join",
        data: this.$qs.stringify({
          detailProductId: this.productId,
          detailProductCount: this.num,
          detailProductUnit: this.unit,
          detailProductPrice: this.price,
          detailComment: this.teast,
          detailUserId: this.userId,
          detailTotalPrice: (this.price * this.num).toFixed(2)
        })
      }).then(res => {
        let result = res.data;
        if (result.status == 200) {
          alert("进入购物车");
          self
            .$axios({
              method: "post",
              url: "http://www.wxhwg.com:8080/mock/api/order/commit",
              data: this.$qs.stringify({
                orderUserId: self.userId,
                orderTotalPrice: self.price,
                orderComment: self.teast
              })
            })
            .then(res => {
              let result = res.data;
              if (result.status == 200) {
                const h = this.$createElement;
                this.$message({
                  message: h("p", null, [
                    h("span", null, "下单成功！！！"),
                    h("i", { style: "color: teal" })
                  ])
                });
              } else {
                const h = this.$createElement;
                this.$message({
                  message: h("p", null, [
                    h("span", null, "添加购物车失败！！！"),
                    h("i", { style: "color: teal" })
                  ])
                });
              }
            });
        } else {
          const h = this.$createElement;
          this.$message({
            message: h("p", null, [
              h("span", null, "添加购物车失败！！！"),
              h("i", { style: "color: teal" })
            ])
          });
        }
      });
    }
  }
};
</script>

<style scoped>
.productdetail p:hover {
  cursor: pointer;
}
.productdetail ul {
  align-self: center;
  list-style: none;
}
.productdetail li {
  text-align: left;
  line-height: 1.5em;
}
.bg-purple-bottom {
  border-radius: 5px;
  min-height: 60px;
}

.productdetail .el-row {
  margin-bottom: 10px;
}
.productdetail .el-col {
  border-radius: 5px;
}

.productdetail .grid-content {
  border-radius: 5px;
  min-height: 100px;
}
.productdetail .row-bg {
  padding: 10px 0;
}
.productdetail .el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.productdetail .el-icon-arrow-down {
  font-size: 12px;
}
.productdetail .demonstration {
  display: block;
  font-size: 14px;
  margin-bottom: 20px;
}
.productdetail .el-carousel__item h3 {
  font-size: 14px;
  opacity: 0.75;
  line-height: 60px;
  margin: 0;
}

.productdetail .el-carousel__item {
  text-align: center;
}
</style>
