// 商品单个类别 可复用展示栏
<template>
    <div class="cproduct" >
        <h2 style=" height:50px;">{{title}}</h2><span style="height:50px; text-align:right;"><a type="text" style="font-size:18px;">查看更多</a></span>

        <div class="cprobox">
           <el-row style="  text-align: center;">
        <el-col :span="8"  v-for="item in list" :key="item.productId" style="height:350px;width:260px;padding:5px">
            <el-card shadow="hover" :body-style="{ padding: '4px', width:'240px',height:'320px'}">

                <router-link :to="{path : '/productdetail', query : {id : item.productId}}">
                    <img v-bind:src="web_static_server + item.productPhoto" class="image">
                </router-link>

                <div style="padding:0px;height:90px;">
                    <span style="font-size: 15px;">{{item.productName}}</span><br>
                    <span style="color:red; ">￥{{item.productSmallPrice}}元</span>
                    <div class="bottom clearfix">
                    <el-button @click.native="AddShopCar(item.productId)" type="text" class="button">加入购物车</el-button>
                    </div>
                </div>
            </el-card>
        </el-col>
        </el-row> 
        </div>

    </div>
</template>
<script>
export default {
    name:'products',
    data(){
        return{
            list:[],
            web_static_server:'http://www.wxhwg.com:8080/mock/static/',
        }
    },
    props:{
        category:String,
        title:String
    },
    created(){
        this.init();
    },
    methods:{
        init(){
            this.$axios.get("http://www.wxhwg.com:8080/mock/api/product/selectByCatrgory",{
                params:{
                    categoryId:this.category,
                    pageSize:8
                }
            }).then(response=>{
                let result=response.data;
                if(result.status==200){
                    this.list=result.data.list;
                }
            }).catch(error=>{

            });
        },
        show(id){
            //push带有后退结果 replace没有后退
            this.$router.push({
                name:'product',
                query:{id}
            });
        },
        // // 添加购物车
        AddShopCar(productId){
            // 判断登录状态
            let user = JSON.parse(this.$store.state.loginUser);
            if(!user){
                var mymessage=confirm("请先登录账号！");
                if(mymessage==true){   
                    this.$router.push({path:'/login'});
                }
                return;
            }

            let uuid = JSON.parse(this.$store.state.loginUser).userId;

            // 查询单个商品信息
            this.$axios
            .get("http://www.wxhwg.com:8080/mock/api/product/find", {
                params: {
                    productId : productId
                }
            })
            .then(response => {
                let result = response.data;
                if (result.status == 200) {
                    let currentProduct = result.data;
                    
                    // 加入购物车
                    this.$axios({
                        method: "post",
                        url: "http://www.wxhwg.com:8080/mock/api/detail/join",
                        data: this.$qs.stringify({
                            detailProductId: currentProduct.productId, // 传参
                            detailProductCount: 1,
                            detailProductUnit: currentProduct.productLargerUnit,
                            detailProductPrice: currentProduct.productSmallPrice,
                            detailComment: "",
                            detailUserId: uuid
                        })
                    }).then(res => {
                        let result = res.data;
                        if (result.status == 200) {
                            console.log(result.message);
                            this.$message.success("加入购物车成功！");
                        } else {
                            this.$message.warning("加入购物车失败！");
                        }
                    });

                }
            })
            .catch(error => {
                alert(error);
            });
            
        }






    }
}
</script>
<style scoped>

 .cproduct{

     height: 810px;
     /* padding-top: 50px; */
     background-color: #f6f6f7
    
}

.cprobox{
    width: 100%;
    height: 700px;
}
  .cproduct .bottom {
    margin-top: 13px;
    line-height: 12px;
    
  }

  .cproduct .button {
    padding: 0;
    float: right;
  }
 .cproduct span{
    padding-bottom:7px;
}
  .cproduct .image {
    padding-top:10px;
    padding-bottom:5px;
    padding-left: 10px; 
    width: 93%;
    height: 240px;

    display: block;
  }

  .cproduct .clearfix:before,
  .cproduct .clearfix:after {
      display: table;
      content: "";
  }
  
  .cproduct .clearfix:after {
      clear: both
  }

</style>

