<template>
<div class="querycategory">
<el-table
    :data="tableData"
    style="width: 100%">
       <el-table-column
      label="顺序"
      width="180">
    <template slot-scope="{row,$index}">
  <el-input  type="number" :disabled="true" v-model="tableData[$index].categoryOrder" :step="1"  :min="1" :max="5" >
          <el-button v-if="showEdit[$index]"	 @click="dec" slot="prepend" icon="el-icon-minus"></el-button>
         <el-button v-if="showEdit[$index]"	 @click="add" slot="append" icon="el-icon-plus"></el-button>
  </el-input>
</template>
    </el-table-column>
    <el-table-column
    prop="categoryId"
      label="编号"
      width="180">  
    </el-table-column>
    <el-table-column
        prop="categoryName"
      label="名称"
      width="180">
        <template slot-scope="{row,$index}">
            <input class="edit-cell" v-if="showEdit[$index]"    v-model="row.categoryName">
            <span v-if="!showEdit[$index]">{{row.categoryName}}</span>
        </template>
    </el-table-column>
      <el-table-column
      prop="categoryStatus"
      label="状态"
      width="180">
        <template slot-scope="{row,$index}">
            <input class="edit-cell" v-if="showEdit[$index]"    v-model.number="row.categoryStatus" :min="0" :max="1">
            <span v-if="!showEdit[$index]">有效</span>
        </template>
      </el-table-column>
    <el-table-column label="操作">
       <template slot-scope="{row,$index}">
            <el-button type="text" size="small"     @click.native="handleUpdate($index, row)"     v-if="showBtn[$index]">更新</el-button>
            <el-button type="text" size="small"     @click.native="handleCancel($index, row)"     v-if="showBtn[$index]">取消</el-button> 
            <el-button type="text" size="small"     @click.native="handleEdit($index, row)"       v-if="!showBtn[$index]">编辑</el-button>
            <el-button type="text" size="small"     @click.native="handleDelete($index, row)"     v-if="!showBtn[$index]">删除</el-button>
        </template>
    </el-table-column>
  </el-table>
  </div>
</template>
<script>
export default {
  name:"test",
   data() {
      return {
        number:0,
        tableData:[],
        showEdit:[], //显示编辑框
        showBtn:[],
        showBtnOrdinary: true,

      }
    },
   created(){
       this.init();
  },
    methods: {
      // 初始化查询类别列表
      init(){
        let self=this;
        this.$axios({
          method:'get',
          url:'http://www.wxhwg.com:8080/mock/category/qryList',  
        }).then(response=>{
  if(response.data.status=='200'){
       this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
            self.tableData=response.data.data;
          }
        })
      },
      dec(){
        let self=this;
       if(this.tableData[this.number].categoryOrder==0){    
           this.$message({
           message:'值不能小于0',
           center:true,
           duration:1000,
           type:1000,
         })
       }
       
       else{
         this.tableData[this.number].categoryOrder--;
          self.$axios({
           method:'GET',
           url:'http://www.wxhwg.com:8080/mock/category/downdateById',
           params:{
             categoryId:self.tableData[self.number].categoryId
           }
         }).then(response=>{
           self.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })      
      })
       }
      },
      add(){
        let self=this;
     if(this.tableData[this.number].categoryOrder==5){ 
         this.$message({
           message:'最大为5',
           center:true,
           duration:1000,
           type:1000,
         })
       }
       else{
         this.tableData[this.number].categoryOrder++;
         self.$axios({
           method:'GET',
           url:'http://www.wxhwg.com:8080/mock/category/updateById',
           params:{
             categoryId:self.tableData[self.number].categoryId
           }
         }).then(response=>{
           self.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
         })
       }
      },
      handleEdit(index, row) {
        this.number=index;
        this.$set(this.showEdit,index,true)
        this.$set(this.showBtn,index,true)
      },
      handleDelete(index, row) {
        //  this.$message({
        //    message:"功能未开启",
        //    center:true,
        //    duration:1000,
        //    type:1000,
        //  })
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // 删除类别
       this.$axios({
         method:'GET',
         url:'http://www.wxhwg.com:8080/mock/category/delById',
        params:{
          categoryId:row.categoryId
        }
       }).then(response=>{
          if(response.data.status=='200'){
          this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         })
         this.tableData.splice(index,1);
          }
       })         
        this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      },
      // 取消编辑
      handleCancel(index, row) {
        this.$set(this.showEdit,index,false)
        this.$set(this.showBtn,index,false)
           },
                  
      //点击更新
      handleUpdate(index, row) {  
             let self=this;
          this.$axios({
            method:'post',
            url:'http://www.wxhwg.com:8080/mock/category/update',
            data:this.$qs.stringify({
              categoryId:row.categoryId,
              categoryName:row.categoryName,
              categoryOrder:row.categoryOrder,
              categoryStatus:row.categoryStatus
            })
            }).then(response=>{
         this.$message({
           message:response.data.message,
           center:true,
           duration:1000,
           type:1000,
         }) 
               this.$set(this.showEdit,index,false)
               this.$set(this.showBtn,index,false)
            })       
      }, 
    }
  }
</script>
<style>
    .querycategory input::-webkit-outer-spin-button,
    .querycategory input::-webkit-inner-spin-button {
        -webkit-appearance: none ;
        appearance: none ; 
        margin: 0 ; 
    }

    .querycategory .el-input.is-disabled .el-input__inner{
          background-color: #fff;
          color: #666;
      }
</style>
