<template>
    <div class="adduser">
        <el-col :span="10">
             <i-showuser ref="addForm"></i-showuser>
             <el-col>
                <el-form label-width="100px">
                <el-form-item label="">
                    <el-button type="primary" @click="submitForm('addForm')">立即创建</el-button>
                    <el-button @click="resetForm('addForm')">重置</el-button>
                </el-form-item>
                </el-form>
            </el-col>
        </el-col>
    </div>
</template>


<script>
import ShowUser from './ShowUser.vue'
  export default {
      components:{
          "i-showuser" : ShowUser
      },
      data(){

      },
      methods:{
            // 提交表单
            submitForm(formName) {
                console.log(this.$refs['addForm']);
                // 获取子控件的form VueComponent对象
                let formComp = this.$refs[formName].getForm();
                let formData = this.$refs[formName].getFormData();
                formComp.validate((valid) => {
                if (valid) {
                    // 创建用户
                    let params = this.$qs.stringify({
                            userUserName : formData.userUserName,
                            userPassword : formData.userPassword,
                            userTelphone : formData.userTelphone,
                            userAddress : formData.userAddress,
                            userShopName : formData.userShopName,
                            userComment : formData.userComment,
                            userStatus : formData.userStatus
                        });
                    this.$axios.post("http://www.wxhwg.com:8080/mock/user/add",params)
                    .then(response=>{
                        let result = response.data;
                        if(result.status == "200"){
                            this.$message({
                                message: '注册用户成功！',
                                type: 'success'
                            });
                        }else{
                            alert("注册失败！");
                        }
                    }).catch(error=>{
                        alert(error);
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            // 重置表单
            resetForm(formName) {
                let formComp = this.$refs[formName].getForm();
                formComp.resetFields();
            }
      }
  }
</script>